body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
  width: 100%;
  height:100%;
}

#container {
  width: 100%;
  height:100%;
  position: absolute;
  background: url('https://www.dropbox.com/s/6yizytmgo2ao4ho/united-states-california-yosemite-national-park-tree-mountain-sky-clouds-night-sunset-forest-spring-april.jpg?dl=1') no-repeat fixed;
  background-size: cover;
  display: flex;
  justify-content: center;
  
}

#quote-box{
  background-color: black;
  height:fit-content;
  min-height: 200px;
  width: 70%;
  min-width: 300px;
  display: grid;
  position: relative;
  top: 20%;
  grid-template-rows:3fr 1fr 2fr ;
  background-color: rgba(255, 255, 255, 0.5);
  box-shadow: -10px 15px 15px #000;
  font-family: 'New Tegomin', serif;
  border-radius: 20px;
  padding: 10px;
  transition: all ease 0.5s;
}

#text{
  font-size: 30px;
  color: black;
  font-weight: 600;
  margin: 10px;   
}

#author{
  font-size: 20px;
  font-weight: 600;
  text-align: right;
  margin-right: 10%;
}

.button{
  height: 30px;
  width: 30px;
}

#button{
  display: flex;

}

#tweet-quote{
  padding-left: 10px;
  flex: 0;
}

#fb-post{
  flex: 2;
}
#new-quote{
  flex: 2;
  padding-right: 20px;
}

#new-quote .btn{
  float: right;
}

@keyframes shift {
  0%{opacity: 0;}
  100%{opacity:1;}  
}